import { Component } from '@angular/core';
import { ActiveModalRef } from '@bmc-ux/adapt-angular';
import { IProgressIndicatorModalConfiguration } from './progress-indicator-modal.interface';
import { ProgressIndicatorStatus } from './progress-indicator-status.enum';

@Component({
  selector: 'rx-progress-indicator-modal',
  templateUrl: './progress-indicator-modal.component.html',
  styleUrls: ['./progress-indicator-modal.component.scss']
})
export class ProgressIndicatorModalComponent {
  config: IProgressIndicatorModalConfiguration;
  deploymentStatus = ProgressIndicatorStatus;

  constructor(public context: ActiveModalRef) {
    this.config = this.context.getData();
  }

  close(): void {
    this.context.close(false);
  }
}
